import { createGlobalStyle } from "styled-components";

export default createGlobalStyle`
  * {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Marvel, sans-serif;
  background: #f0f0f5;
}

input,
button {
  font: 400 18px;
}

button {
  cursor: pointer;
}

`;
